// 

import './index.css'
// import $ from 'jquery'
import { ModalServer } from '@/components'
import { fetch } from '@/util/request'

export default class ComTable{
   /**
   * 
   * @param  {Object} btn   
   * @param  {String} type  
   * @param  {String} type  
   * @param  {object} type  
   * @return {void}   void
   */
  bindClickEvent = (typeData, urls, headers) => {
      const url = urls
      // 
      typeData.currentPage = 0
      fetch(url, typeData, dataSource => {
        console.log(typeData)
        const modal = new ModalServer({
          headers: headers,
          // 
          onPrev: () => {
            if (typeData.currentPage > 0) {
              typeData.currentPage -= 1
              fetch(url, typeData, data => {
                modal.renderTable(data.detail)
              })
            } else {
              alert('!')
            }
            console.log(typeData)
          },
          // 
          onNext: () => {
            typeData.currentPage += 1
            fetch(url, typeData, data => {
              if (data.detail.length > 0) {
                modal.renderTable(data.detail)
              } else {
                typeData.currentPage -= 1
                alert('!')
              }
              console.log(typeData)
            })
          }
        })
        modal.init(dataSource.detail)
      })
  }
}


