import './index.css'
import $ from 'jquery'
import hbs from './index.hbs'
import { fetchInterval } from '@/util/request'
import { deformationToOrigin } from '@/util/zoom/zoom'

import ComTable from '../comTabel'

let comTable = new ComTable()
export default class ThreatenList {
  constructor() {
    this.currentPage = 0
  }

  render(type, troopsId) {
    let tcHeaders = ['IP', '', '', '', '', '']
   
    fetchInterval('fetchIntranetVirusTab', {type, troopsId}, data => {
      // 
      let headers = ['', '', '', '']
      $('#virusTable').empty().append(hbs({data, headers}))
      if(data.list.length === 0) {
        $('#virusTable').append('<span class="com-hint"></span>')
      }
      const btn = $('#virusTable .attachment')
      btn.on('click', e => {
        const id = $(e.currentTarget).attr('data-id')
        const limit = 8
        const currentPage = 0
        let typeData = {type, id, limit, currentPage, troopsId}
        comTable.bindClickEvent(typeData, 'fetchIntranetVirusTabList', tcHeaders)
      })
      // 
      $('.intra-virus-table .mouse-hover')
        .on('mousemove', ev => {
          let Ev = ev || window.event
          let pos = deformationToOrigin([Ev.pageX , Ev.pageY])
          $('.com-floating .value').html($(Ev.currentTarget).attr('data-title'))
          $('.com-floating').stop(true, true).fadeIn(500)
          $('.com-floating').css({'transform': `translate(${pos[0] + 30}px, ${pos[1] + 30}px)`})
        }).on('mouseout', () => {
          $('.com-floating .value').empty()
          $('.com-floating').stop(true, true).fadeOut(500)
        })
    }, 600000)
  }
}



